:root {
  --off-white: #fafafa;
}

.nav {
    background-color: #fafafa;
    background-color: var(--off-white);
    position: fixed;
    top: 0px;
    width: 100%;
    height: 80px;
    z-index: 1000;
    box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
  }
  
  .nav-logo {
    position: relative;
    height: 50px;
    width: 50px;
    left: 2%;
  }
  
  .nav-logo:hover {
    -webkit-transition: .5s;
    transition: .5s;
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    cursor: pointer;
  }
  
  .nav .nav-content {
    max-width: 900px;
    padding: 0rem 3rem;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }
  
  #mynav-item {
    height: 100%;
    width: 100%;
    /* display: inline; */
    color: #333;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding-right: 25px;
    padding-left: 25px;
  }
  
  #mynav-item:hover {
    background-color: lightgrey;
    -webkit-transition: .3s;
    transition: .3s;
    cursor: pointer;
  }

#resume {
    position: absolute;
    top: 0;
    right: 2.5%;
    width: 50px;
    height: 50px;
    display: table-cell;
    vertical-align: middle;
    -webkit-transition: all 0s ease-in-out;
    transition: all 0s ease-in-out;
    z-index: 10;
    border-radius: 50%;
    background-color: #EBEBEB;
    text-align: center;
}

#resume:hover{
    -webkit-transition: .3s;
    transition: .3s;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.resume-icon{
    padding-top: 10px;
    padding-left: 3px;
    opacity: 100%;
    color: #858585;
    z-index: 11;
}

.resume-download-text{
    visibility: hidden;
    width: 120px;
    background-color: lightgrey;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    right: 105%;

    /* Position the tooltip */
    position: absolute;
    z-index: 10;
}

.resume:hover .resume-download-text{
    visibility: visible;
}

#icon:hover{
    color: #858585;
}
