  .box {
    position: absolute;
    width: 99%;
    height: 92%;
    top: 53%;
    padding: 5px;
    border: 2px solid #D3D3D3	;
    }
  .box::before, .box::after {
    content: "•";
    position: absolute;
    color: #D3D3D3	;
    border: 2px solid #D3D3D3	;
    line-height: 12px;
  }
  .box::before {
    left: 5px;
  }
  .box::after {
    right: 5px;
  }
  .box .box-inner {
    position: relative;
    border: 2px solid #D3D3D3	;
    height: 100%;
  }
  .box .box-inner::before, .box .box-inner::after {
    content: "•";
    position: absolute;
    color: #D3D3D3;
    border: 2px solid #D3D3D3;
    line-height: 12px;
    bottom: -2px;
  }
  .box .box-inner::before {
    left: -2px;
  }
  .box .box-inner::after {
    right: -2px;
  }
